import React, { useState, useEffect } from "react";
import axios from "axios";
import { saveAs } from "file-saver";
import { jsPDF } from "jspdf";
import "jspdf-autotable";
import './App.css'
const ErrorMessage = ({ msg }) => {
    return (
      <div style={{
        color: 'red',
        backgroundColor: 'pink',
        padding: '1rem',
        borderRadius: '0.5rem'
      }}>
        {msg}
      </div>
    )
  };
const App = () => {
  const [data, setData] = useState([]);
  const [schoolCode, setSchoolCode] = useState('');
  const [baseRollNum, setBaseRollNum] = useState('');
  const [startRoll, setStartRoll] = useState('');
  const [lastRoll, setLastRoll] = useState('');
  const [fetchResult, setFetchResult] = useState(false);
  const [error, setError] = useState('');

  useEffect(() => {
    const fetchData = async () => {
      for (let i = startRoll; i <= lastRoll; i++) {
        const rollToFetch = (Number(baseRollNum) +  Number(i));
        const response = await axios.post(`http://matricbseb.com/`, {
          
            txtRoleCode: schoolCode,
            txtrollno: `${rollToFetch}`,
            // numberOfApiCalls:numberOfApiCalls
        });
        setData((prevData) => [...prevData, response.data]);
      }
    };
    if (fetchResult) {
      fetchData();
      setFetchResult(false);
    }
    
  }, [fetchResult]);

  


  const exportPdf = () => {
    const doc = new jsPDF();
    const tableColumns = ["Column 1", "Column 2", "Column 3", "Column 4", "Column 5"];
    const tableData = data.map((row) => [      row.column1,      row.column2,      row.column3,      row.column4,      row.column5,    ]);
    doc.autoTable({
      head: [tableColumns],
      body: tableData,
    });
    doc.save("table.pdf");
  };
  const showTable = () => { 
    return (
      <div>
 <div style={{ textAlign:"right", marginRight: '10px'}}>
        <button onClick={exportPdf}>Export to PDF</button>
        </div>
      <table>
        <thead>
          <tr>
              <th>Roll Number</th>
              <th>Name</th>
              <th>Father's Name</th>
              <th>Mother's Name</th>
              <th>Result</th>
            </tr>
        </thead>
        <tbody>
          {data.map((row) => (
            <tr key={row.id}>
              <td>{row.column1}</td>
              <td>{row.column2}</td>
              <td>{row.column3}</td>
              <td>{row.column4}</td>
              <td>{row.column5}</td>
            </tr>
          ))}
        </tbody>
      </table>
      </div>
     
    )
  }

  return (
    <>
       <h1 style={{ fontWeight: "bold", textAlign:"center" }}>BSEB Examination Result-2023</h1>
      <div style={{ fontWeight: "bold", textAlign:"center", marginBottom:"15px"}}>
        <div style={{ fontWeight: "bold", textAlign:"center", marginBottom:"15px"}}>
          <label htmlFor="schoolCode">School Code:</label>
        <input
          type="number"
          id="schoolCode"
          value={schoolCode}
          onChange={(e) => setSchoolCode(e.target.value)}
        />
        </div>
        <div style={{ fontWeight: "bold", textAlign:"center", marginBottom:"15px"}}>
          <label htmlFor="baseRollNum">Base Roll Number:</label>
        <input
          type="number"
          id="baseRollNum"
          value={baseRollNum}
          onChange={(e) => setBaseRollNum(e.target.value)}
        /></div>
        <div style={{ fontWeight: "bold", textAlign:"center", marginBottom:"15px"}}>
          <label htmlFor="startRoll">Start Roll:</label>
        <input
          type="number"
          id="startRoll"
          value={startRoll}
          onChange={(e) => setStartRoll(e.target.value)}
        />
        </div>
        <div style={{ fontWeight: "bold", textAlign:"center", marginBottom:"15px"}}>
        <label htmlFor="lastRoll">Last Roll:</label>
        <input
          type="number"
          id="lastRoll"
          value={lastRoll}
          onChange={(e) => setLastRoll(e.target.value)}
        />
        </div>
        {error && <ErrorMessage msg={error}/>}
         <button onClick={()=>setFetchResult(true)}>Fetch Data</button>
      </div>
     {data && data.length>0 && showTable()}
      
       <footer style={{ marginTop: "50px", textAlign: "center" }}>
        <p>&copy; 2023 Manavjeet Gupta. All rights reserved.</p>
      </footer>
    </>
  );
};

export default App;